.container {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f0f0f0;
  }
  
  .login-form {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 85vw;
    height: 80vh;
    padding: 20px;
    background-color: white;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .imageContainer {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%; 

  }
  .formContainer{
    flex:1;
    gap: "15%";
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    /* justify-content: space-around; */
    height: 100%;
    padding-top: 5%;
    padding-left: 5%
  }
   .formContainer > * { 
    margin-bottom: 15%;  
  } 
  
  .imageContainer {
    /* background-color: green; */
  }
  
  /* .formContainer {
    background-color: yellow;
  } */
  .webcam {
    display: flex;
    flex:1;
    width: 100%;
    height: 120%;
  }
  .captureButton {
    background-color: #2196f3; /* Primary blue background color */
    color: white; /* White text color */
    border: none; /* Remove button border */
    padding: 10px 20px; /* Add padding */
    font-size: 22px; /* Set font size */
    border-radius: 4px; /* Add border radius for rounded corners */
  }
  
  .captureButton:hover {
    background-color: #0c7bc5; 
    cursor: pointer;
  }

  label {
    display: block;
    margin-bottom: 5px;
  }
  
  input {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 3px;
    box-sizing: border-box;
  }

  .loginButton {
    background-color: #2196f3; /* Primary blue background color */
    color: white; /* White text color */
    border: none; /* Remove button border */
    padding: 10px 20px; /* Add padding */
    font-size: 16px; /* Set font size */
    border-radius: 4px; /* Add border radius for rounded corners */
    cursor: pointer; /* Change cursor to pointer on hover */
  }
  
  .loginButton:hover {
    background-color: #0c7bc5; /* Darker shade of primary color on hover */
  }

  #email{
    margin-bottom: 5%;
  }
  
  /* YourStyles.css */

.loginText {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  margin-top: 20px; /* Adjust margin as needed */
}

.loginText span:first-child {
  margin-right: 10px; /* Adjust margin between the two spans */
}

.loginText span:last-child {
  color: blue; /* Adjust color for the 'Login' text */
  cursor: pointer; /* Change cursor to pointer on hover */
}

.loginText span:last-child:hover {
  text-decoration: underline; /* Underline 'Login' text on hover */
}

  